import React, { Component } from "react";
import styled from "styled-components";

const Div = styled.div`
  text-align: center;
`;

const Circle = styled.div`
  width: 10px;
  height: 10px;
  border: 1px solid black;
`;

class Loading extends Component {
  render() {
    return (
      <Div>
        <Circle />
      </Div>
    );
  }
}

export default Loading;
